@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,400;0,500;0,600;0,700;0,800;0,900;1,600&family=Roboto:wght@100;300;400;500;700;900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Space+Mono:ital,wght@0,400;0,700;1,400&display=swap');
@font-face {
    font-family: "Sofia";
    src: url("./src/assets/images/fonts/sofiapro-light.otf");
}

html, *, *::after,*::before { 
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body { 
    width: 100vw;
    max-width: 100%;
    font-family: 'Poppins', sans-serif;
    font-family: 'Roboto', sans-serif;
}

header { 
    display: flex;
    width: 100%;
    max-width: 1440px;

    margin: auto;
    justify-content: space-between;
    align-items: center;
    border-bottom: .5px solid lightgrey;
}

.svglogo { 
    max-width: 100px;
    max-height: 100px;
    padding: 10px;
}

.svglogo > * { 
    fill: #ed1c24;
}

/* menu */
nav { 
    flex-grow: 1;
}

nav ul { 
    list-style :none;
    display: flex;
    justify-content: center;
}

nav ul li { 
    padding: 0 20px;
}
nav ul li a{ 
    text-decoration: none;
    color: black;
    font-size: 20px;
    font-weight: bold;
}

.contact-us { 
    font-size: 20px;
    padding: 20px;
    color: white;
    background-color: #ed1c24;
    text-decoration: none;
    border-radius: 12px;
    font-weight: bold;

}

.hero-section { 
    /* background-color: rgba(255, 0, 0, 0.336); */
    max-width: 1200px;
    height: 80vh;
    margin: auto;
    margin-top: 40px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.hero__image img{ 
    width: 100%;
    max-width: 550px;
}

.hero__title { 
    color: #ed1c24;
    font-size: 5rem;
    font-weight: 900;
    font-family: 'Roboto', sans-serif;
}

.hero-section .text__wrapper h3 { 
    font-weight: bold;
    font-size: 3rem;
}

.hero-section .text__wrapper p{
    margin-top: 10px;
    font-size: 1.5rem;
    color: grey;
}


/* services */

.services { 
    width: 100%;
    max-width: 1440px;
    margin: auto;
    font-family: Sofia, sans-serif; 
}
.services .text-wrapper h1{ 
    font-size: 3rem;
    text-align: center;
    margin-bottom: 30px;
    text-decoration: underline;
    text-decoration-color: #ed1c24;
}

.services__list { 
    width: 100%;
    
    max-width: 1115px;
    margin: 0 auto;
    padding: 30px 100px;
}

.services__list .service__card { 
    padding: 80px 0;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 20px;
}

.service__card .title { 
    width: 100%;
    max-width: 290px;
    font-style: normal;
    font-weight: normal;
    font-size: 40px;
    line-height: 1.2;
    letter-spacing: -0.02em;
    /* color: #ed1c24; */
}
.service__card .service__info{
    padding: 30px;
    background-color: #ff000014;
    border-radius: 12px;
}
.service__card .service__info p { 
    font-family: 'Space Mono', monospace;    /* color: #000; */
    font-style: normal;
    font-weight: normal;
    font-size: 13px;
    line-height: 1.6;
    letter-spacing: 0.02em;
    margin-top: 20px;

}

/* work section */
.work { 
    width: 100%;
    max-width: 1200px;
    margin: auto;
    margin-top: 30px;

    font-family: Sofia;
}

.work > h1 , .services .text-wrapper h1, .about > h1 , .whyus > h1, .contact > h1{ 
    font-size: 4rem;
    font-family: Sofia;
    text-decoration: underline;
    text-align: center;
}
.work > h1 strong , .services .text-wrapper h1 strong, .about >h1 strong , .whyus > h1 strong , .contact > h1 strong{
    color: red;

}

.marketing { 
    margin-top: 50px;
}
.marketing > h1 , .ads >h1{ 
    font-weight: lighter;
    text-decoration: underline;
    text-decoration-color: rgba(255, 0, 0, 0.455);
    padding: 30px;
}

.marketing__wrapper { 
    display: flex;
    gap: 1rem;
    justify-content: space-between;
}
.marketing__wrapper > div { 
    padding: 30px;
    border-radius: 12px;
}
.marketing__wrapper > div > .info p { 
    padding: 15px 20px;
    font-family: 'Space Mono', monospace;
}

.marketing__wrapper .newton-section { 
    background-color: #e8fcfb;
}
.garol-section{ 
    background-color: #ffc8642b;
}
.marketing__wrapper > div > img { 
    max-width: 100%;
    width: 300px;
}

/* ads  */
.ads img { 
    width: 100%;
    max-width: 100px; 
}
.ads-wrapper { 
    display: flex;
    gap: 1rem;
}


.ads-card { 
    background-color: lightgreen;
    padding: 15px;
    padding-top: 30px;
    border-radius: 12px;
}

.ads-card .img-wrapper {
    text-align: center;
    height: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
}


.ads-card p { 
   font-family: Space Mono, monospace;
   padding: 20px 4px;
   font-size: 15px;
   line-height: 1.7rem;
}
.ads-card.mansmatters {
    background-color: #79c7e340;
}

.ads-card.otpless { 
    background-color: #32cd323d;
}

.ads-card.betenx { 
    background-color: #0000002e;
}
.ads-card.arjun { 
    background-color: #ffff6745;
}


/* about sectoin  */
.about  { 
    width: 100%;
    max-width: 1200;
    margin: auto;
    margin-top: 70px;
    font-family: Sofia;
}
.our-team > h3 { 
    width: 100%;
    max-width: 300px;
    text-align: center;
    margin: auto;
    align-self: center;

    font-size: 27px;
}
.about img { 
    width: 100%;
    max-width: 300px;
}

.about .our-team { 
    display: flex;
    justify-content: space-around;
    margin-top: 30px;
    
}
.member-title { 
    padding: 15px 0;
}
.member-title h5 { 
    color: grey;
    margin-top: 5px;
}
.member-title span { 
    display: flex; 
    justify-content: center;
    gap: 2rem;
    margin-top: 10px;
}
.member-title span a { 
    color: black;
}


/* why us  */

.whyus { 
    max-width: 1200px;
    margin: auto;
    margin-top: 150px;
}

.whyus .wrapper {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-template-rows: repeat(3, 1fr);
    grid-column-gap: 0px;
    grid-row-gap: 0px;
    justify-items: center;
    margin-top: 50px;
}
.whyus .wrapper > div { 
    background-color: rgba(223, 1, 1, 0.082);
    padding: 30px 15px;
    border-radius: 12px; 

    display: flex; 
    align-items: center;
    /* align-self: center; */
}
.whyus .wrapper > div p { 
    font-family: monospace;
    font-size: 17px;
    text-align: center;
}

.whyus .wrapper > div i{ 
    display: flex;
    justify-content: center;
    padding: 30px 0;
}
    
    .one { grid-area: 1 / 1 / 2 / 2; }
    .two { grid-area: 1 / 5 / 2 / 6; }
    .three { grid-area: 2 / 3 / 3 / 4; }
    .four { grid-area: 3 / 1 / 4 / 2; }
    .five { grid-area: 3 / 5 / 4 / 6; }


/* contact us  */

.contact { 
    width: 100%;
    max-width: 1200px;
    margin: auto;
    margin-top: 100px;
}

.contact__form { 
    margin-top: 50px;
}

.contact__form h4 { 
    font-size: 20px;
    text-align: center;
    color: grey;
    text-decoration: underline;
    text-decoration-color: black;
    font-family: monospace;

}
.contact__form  form { 
    /* width: 100%; */
    /* max-width: 700px; */
    margin: auto;
    text-align: center;
}


form .input {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 10px 0;

    width: 100%;
    margin: auto;
    max-width: 500px;
}
form .input  label { 
    font-family: monospace;
    font-size: 20px;
    color: black;
}

form input, select, textarea { 
    padding: 10px 5px;
    width: 100%;
    max-width: 450px;
    border-radius: 8px;
}

form button { 
    background-color: #ed1c24; 
    padding: 10px 30px;
    color: white;
    font-weight: bold;
    font-size: 14px;
    font-family: Sofia;
    border-radius: 12px;
    border: 0px solid transparent;
}

/* footer */

footer {
    width: 100%;
    background-color: black;
    color: white;
    text-align: center;
    margin-top: 50px;
}


footer .social-media .wrapper  { 
    margin: 30px;
    
}
footer .social-media .wrapper span a { 
    color: white;
    margin: auto 20px;
}
.footer-main {
    padding: 50px 0;
}
footer-main .book-slot a { 
    color: white;
}

footer .direct-contact { 
    width: 100%;
    max-width: 1200px;
    margin: auto;
    display: flex;
    flex-direction: column;
    font-size: 1.2rem;
    padding: 50px 0 ;
    gap: 4rem;
    margin-top: 30px;
    
}
footer .direct-contact a  {
    text-decoration: none;
    color: rgba(255, 255, 255, 0.856);
    
}

footer .book-slot a {
    color: white;
    background: #ff000054;
    padding: 30px;
    font-size: 1.3rem;
    border-radius: 40px;
    text-decoration: none;

    font-size: 1.4rem;
    font-family: Roboto;
}
